  .close-btn {
    position: absolute;
    font-size: 20px;
    top: 40px; /*25 */
    right: 50px; /*20 */
    z-index: 100000;
    border: 0px;
  }


  @media (max-width: 767px) {
    .modal-content {
      width: 50%;
      height: 50%;
    }

    .close-btn {
      top: 4px;
      right: 2px;
    }
  }

  @media (min-width: 768px) and (max-width: 1023px) {
    /* Add styles for medium-sized screens here */
    .close-btn {
      top: 40px;
      right: 30px;
    }
  }